<template>
    <el-dialog :title="titleShow==true?'添加cdn':'编辑cdn'" :visible.sync="dialogForm" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="账号:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.account"
                        >
                    <el-option label="请选择" value=""></el-option>
                    <el-option
                            v-for="(item ,$key) in account"
                            :label="item.name"
                            :value="item.value"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="name:" :label-width="formLabelWidth">
                <el-input v-model="form.name" auto-complete="off"></el-input>
            </el-form-item>
            <template v-if="!titleShow">
                <el-form-item label="type:" :label-width="formLabelWidth">
                    <el-select
                            size="small"
                            v-model="form.type"
                    >
                        <el-option label="请选择" value=""></el-option>
                        <el-option
                                v-for="(item ,$key) in type"
                                :label="item.name"
                                :value="item.value"
                                :key="$key">
                        </el-option>
                    </el-select>
                </el-form-item>
                    <el-form-item label="detail:" :label-width="formLabelWidth">
                    <el-input v-model="form.detail" auto-complete="off" ></el-input>
                </el-form-item>
                <el-form-item label="enable:" :label-width="formLabelWidth">
                    <el-switch
                            v-model="form.enable"
                            @change="filterAction()"
                            on-color="#13ce66"
                            off-color="#ddd"
                            on-value="1"
                            off-value="3">
                    </el-switch>
                </el-form-item>

            </template>

            <template v-if="titleShow">
                <el-form-item label="cdnType:" :label-width="formLabelWidth">
                    <el-select
                               size="small"
                               v-model="form.cdnType"
                              >
                        <el-option label="请选择" value=""></el-option>
                        <el-option
                                v-for="(item ,$key) in cdnType"
                                :label="item.name"
                                :value="item.value"
                                :key="$key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="sourceType:" :label-width="formLabelWidth">
                    <el-select
                            size="small"
                            v-model="form.sourceType"
                            >
                        <el-option label="请选择" value=""></el-option>
                        <el-option
                                v-for="(item ,$key) in sourceType"
                                :label="item.name"
                                :value="item.value"
                                :key="$key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="sourcePort:" :label-width="formLabelWidth">
                    <el-input v-model="form.sourcePort" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="sources:" :label-width="formLabelWidth">
                    <el-input v-model="form.sources" auto-complete="off" ></el-input>
                </el-form-item>
            </template>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">取 消</el-button>
            <el-button type="primary" @click="submit()">提交</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../core/QuRequest'

    const quRequest = new QuRequest()
    export default {

        data() {
            return {
                type: [
                    { 'name': '智能压缩', 'value': 'SetPageCompressConfig'},
                    { 'name': '过滤参数','value': 'SetIgnoreQueryStringConfig'},
                    { 'name': 'Range回源', 'value': 'SetRangeConfig'},
                    { 'name': '回源host设置', 'value': 'SetSourceHostConfig'}
                ],
                account: [
                    { 'name': '账号一', 'value': '1'},
                    { 'name': '账号二','value': '2'},
                    { 'name': '账号三', 'value': '3'},
                    { 'name': '账号四', 'value': '4'}
                ],
                cdnType: [
                    { 'name': '图片及小文件分发', 'value': 'web'},
                    { 'name': '大文件下载','value': 'download'},
                    { 'name': '视音频点播加速', 'value': 'video'},
                    { 'name': '直播流媒体加速', 'value': 'liveStream'}
                ],
                sourceType:[
                    { 'name': 'IP源站', 'value': 'ipaddr'},
                    { 'name': '域名源站','value': 'domain'},
                    { 'name': 'Bucket源站', 'value': 'OSS'},
                ],
                titleShow:false,
                dialogForm: false,
                formLabelWidth: '200px',
                disabled:true,
                isShow:true,
                form:{
                    account: "3",
                    cdnType: "web",
                    sourceType: "ipaddr",
                    sources:'',
                    name:'',
                    create_time:'',
                    sourcePort:'80',
                    type:'',
                    detail:'',
                    enable:'1'
                }
            }
        },
        mounted() {

        },
        methods: {
            showDialog() {
                this.dialogForm = true;
            },
            showInfoDialog(item) {
                if(item){
                    this.isShow = false;
                    this.form.account= item.account;
                    this.form.name=item.name;
                    this.form.enable=item.enable;
                    this.form.type =item.type;
                    this.form.detail =item.detail;
                    this.titleShow = false;
                }else{
                    this.titleShow = true;
                    this.form.account= '';
                    this.form.name= '';
                    this.form.cdnType = '';
                    this.form.sourceType = '';
                    this.form.sourcePort= '';
                    this.form.sources = '';
                }

                this.dialogForm = true;
            },
            submit(){
                quRequest.send(this.titleShow==true?quRequest.Apis.qutuiba.cdn.addCdnDomain:quRequest.Apis.qutuiba.cdn.modifyCdnDomain, this.form).then(res => {
                    this.$message.success("提交成功！");
                    this.$emit('update')
                }).catch(err => {
                    this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
                this.dialogForm = false;
            }
        }
    }
</script>


